.TodoForm {
    width: 100%;
    margin-bottom: 1rem;
}
    
.todoInput {
    outline: none;
    background: none;
    border: 1px solid rgb(0,200,149);
    padding: 0.5rem 1rem;
    margin-top: 1rem;
    margin-bottom: 2rem;
    width: 300px;
    color: #fff;
}

.todoInput::placeholder {
    color: #ffffff4d;
}

.todoBtn {
    background: rgb(0,200,149);
    color: #fff;
    border: none;
    padding: 0.55rem;
    cursor: pointer;
}

p {
    color: white;
    margin-bottom: 15px;
    font-size: 12px;
}